table, th, td {    
    border:1px solid black ;  
    margin-left: auto;  
    margin-right: auto;  
    border-collapse: collapse;    
    width: 500px;  
    text-align: center;  
    font-size: 20px;  
    max-width: 80%;
  }  
  .center {
    width: 50%;
    margin: auto;
    padding: 10px;
    text-align: center;
  }
    
   .container h2 {
    color: #2a97db; 
   } 
  
   .container hr {
    border-top: 1px solid #ccc;
     margin: 20px 0; 
   }   
